<template>
  <div class="manage">
    <div class="man-info">
      <m-info></m-info>
    </div>
    <div class="man-list">
      <!-- title -->
      <div class="z-flex z-align-item-center filt">
        <span>賬號管理</span>
      </div>
      <!-- 筛选 -->
      <div class="flit z-flex z-justify-content-between">
        <div
          v-for="(flitItem, flitIndex) in flitList"
          :key="flitItem.id"
          class="seletAdminManage z-wid100"
          :class="flitItem.active ? 'active' : ''"
          @click="handleFlitTypeClick(flitIndex)"
        >
          {{ flitItem.scope }}:{{ flitItem.checkedItem.txt }}
          <div class="flitItemWrapper">
            <p
              v-for="(subItem, subIndex) in flitItem.sub"
              :key="subItem.id"
              @click="handleFlitItemClick(subIndex)"
              :class="subItem.id == flitItem.checkedItem.id ? 'active' : ''"
            >
              {{ subItem.txt }}
            </p>
          </div>
        </div>
      </div>
      <!-- 表格 -->
      <div class="table">
        <m-table @choosedData="getChoosedData" />
      </div>
      <!-- 彈窗 -->
      <div>
        <details-alert
          :choosedData="choosedData"
          @choosedDataEmpty="setChoosedData"
        />
      </div>
      <!-- 刪除按鈕 -->
      <div class="delBtn">
        <svg-btn
          class="svgBtn"
          :inColor="delBtn.inColor"
          :outColor="delBtn.outColor"
          :fillColor="delBtn.fillColor"
        />
        <span>刪除</span>
      </div>
    </div>
  </div>
</template>

<script>
import MInfo from "./Modules/Info";
import MTable from "./Modules/Table";
import DetailsAlert from "./Modules/DetailsAlert";
import SvgBtn from "@/components/operate/SvgBtn";

export default {
  data() {
    return {
      // 選中的表格數據
      choosedData: {},
      // 刪除按鈕的顏色
      delBtn: {
        inColor: "#5B6573",
        outColor: "#485463",
        fillColor: "#485463"
      },
      // 篩選欄
      flitList: [
        {
          scope: "廠區",
          val: "factory",
          id: "1",
          active: false,
          checkedItem: { txt: "全部", val: "all", id: "1" },
          sub: [
            { txt: "全部", val: "all", id: "1" },
            { txt: "CD", val: "cd", id: "2" },
            { txt: "SZ", val: "sz", id: "3" }
          ]
        },
        {
          scope: "事業群",
          val: "business",
          id: "2",
          active: false,
          checkedItem: { txt: "全部", val: "all", id: "1" },
          sub: [
            { txt: "全部", val: "all", id: "1" },
            { txt: "SHZBG", val: "SHZBG", id: "2" },
            { txt: "MCEBG", val: "MCEBG", id: "3" }
          ]
        },
        {
          scope: "產品處",
          val: "product",
          id: "3",
          active: false,
          checkedItem: { txt: "全部", val: "all", id: "1" },
          sub: [
            { txt: "全部", val: "all", id: "1" },
            { txt: "Mac(I)", val: "Mac(I)", id: "2" }
          ]
        },
        {
          scope: "類型",
          val: "type",
          id: "4",
          active: false,
          checkedItem: { txt: "全部", val: "all", id: "1" },
          sub: [
            { txt: "全部", val: "all", id: "1" },
            { txt: "發單方", val: "send", id: "2" },
            { txt: "接單方", val: "recv", id: "3" }
          ]
        }
      ]
    };
  },
  methods: {
    // 篩選item點擊事件
    handleFlitItemClick(index) {
      for (let i in this.flitList) {
        if (this.flitList[i].active == true) {
          this.flitList[i].checkedItem = this.flitList[i].sub[index];
          // handleFlitTypeClick(i)
          return;
        }
      }
    },
    // 篩選type點擊事件
    handleFlitTypeClick(index) {
      if (this.flitList[index].active == true) {
        this.flitList[index].active = false;
        return;
      }
      for (let i in this.flitList) {
        this.flitList[i].active = false;
      }
      this.flitList[index].active = true;
    },
    // 設置選中的表格數據
    setChoosedData() {
      this.choosedData = {};
    },
    // 獲取選中的表格數據
    getChoosedData(val) {
      console.log("get");
      this.choosedData = val;
    },
    // 選擇分類
    toggleFlit(val) {
      for (let item of this.flitList) {
        item.active = false;
      }
      switch (val) {
        case "all":
          this.flitList[0].active = true;
          break;
        case "recv":
          this.flitList[1].active = true;
          break;
        case "send":
          this.flitList[2].active = true;
          break;
      }
    }
  },
  components: {
    MInfo,
    MTable,
    DetailsAlert,
    SvgBtn
  }
};
</script>

<style lang="less" scoped>
.manage {
  .man-info,
  .man-list {
    background-color: #eff9ff;
    box-sizing: border-box;
    color: #12222e;
    padding: 20px;
    min-height: 100%;
    min-width: 925px;
  }
  .man-list {
    margin-top: 10px;
  }
  .filt {
    > span {
      margin-right: 1rem;
    }
    > button {
      margin: 0 1rem;
    }
  }
  .delBtn {
    display: inline-block;
    position: relative;
    margin-top: 20px;
    > .svgBtn {
      transform: scaleX(0.8);
    }
    > span {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      font-size: 20px;
      color: #fff;
    }
  }
}
.seletAdminManage {
  background-color: #d3dfe7;
  height: 40px;
  line-height: 40px;
  text-align: center;
  position: relative;
  font-size: 14px;
  color: #12222e;
  cursor: pointer;
  &.active {
    background-color: #91adc0;
    box-shadow: 0px 0px 8px 0px rgba(164, 175, 183, 0.75);
    color: #ffffff;
    &::after {
      transform: rotate(180deg);
    }
    .flitItemWrapper {
      height: auto;
      box-shadow: 0px 0px 8px 0px rgba(164, 175, 183, 0.75);
    }
  }
  &::after {
    content: "";
    position: absolute;
    top: 40%;
    right: 10%;
    border-width: 6px;
    border-style: solid;
    border-color: #2e6e9e transparent transparent transparent;
    transition-duration: 0.5s;
    transform-origin: center 3px;
  }
  &:focus {
    box-shadow: 0px 0px 8px 0px rgba(164, 175, 183, 0.75);
  }
}
.flitItemWrapper {
  position: absolute;
  color: #12222e;
  top: 40px;
  width: 100%;
  z-index: 999;
  background-color: #91adc0;
  overflow: hidden;
  height: 0;
  transition: height 0.5s;
  p {
    margin: 0;
    height: 40px;
    border-top: 1px solid #a4bccd;
    &:hover {
      background-color: #a4bccd;
    }
    &.active {
      background-color: #a4bccd;
      color: #fff;
    }
  }
}
</style>
