<template>
  <div class="table">
    <el-table
      ref="multipleTable"
      :data="tableData"
      tooltip-effect="dark"
      style="width: 100%"
      @selection-change="handleSelectionChange"
      :row-class-name="tableRowClassName"
    >
      <el-table-column type="selection" width="55"> </el-table-column>
      <el-table-column label="用戶名" width="120">
        <template slot-scope="scope">{{ scope.row.username }}</template>
      </el-table-column>
      <el-table-column prop="fctry_zone" label="所屬廠區" width="120">
      </el-table-column>
      <el-table-column prop="dept_name" label="單位名稱"></el-table-column>
      <el-table-column prop="tel" label="固定電話"></el-table-column>
      <el-table-column prop="email" label="Email"></el-table-column>
      <el-table-column prop="create_date" label="申請時間"></el-table-column>
      <el-table-column label="操作">
        <template slot-scope="scope">
          <div @click="showAlert(scope.row)">
            詳細信息
          </div>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
export default {
  data() {
    return {
      tableData: [
        {
          username: "Mac(I)製造處",
          fctry_zone: "成都",
          dept_name: "鸿富锦精密电子(成都)有限公司",
          tel: "5073-66810",
          email: "hzcd-mis-sys1@mail.foxconn.com",
          create_date: "2019-11-18 18:21:34",
          id: "1"
        },
        {
          username: "Mac(I)製造處",
          fctry_zone: "成都",
          dept_name: "鸿富锦精密电子(成都)有限公司",
          tel: "5073-66810",
          email: "hzcd-mis-sys1@mail.foxconn.com",
          create_date: "2019-11-18 18:21:34",
          id: "2"
        },
        {
          username: "Mac(I)製造處",
          fctry_zone: "成都",
          dept_name: "鸿富锦精密电子(成都)有限公司",
          tel: "5073-66810",
          email: "hzcd-mis-sys1@mail.foxconn.com",
          create_date: "2019-11-18 18:21:34",
          id: "3"
        },
        {
          username: "Mac(I)製造處",
          fctry_zone: "成都",
          dept_name: "鸿富锦精密电子(成都)有限公司",
          tel: "5073-66810",
          email: "hzcd-mis-sys1@mail.foxconn.com",
          create_date: "2019-11-18 18:21:34",
          id: "4"
        }
      ],
      multipleSelection: [],
      // 待刪除數據的 index 列表
      delRowIndexList: [],
      styleClass: ""
    };
  },

  methods: {
    // 展開彈框
    showAlert(val) {
      console.log("send");
      this.$emit("choosedData", val);
    },

    //
    tableRowClassName({ row, rowIndex }) {
      // console.log(row)
      // console.log(rowIndex)
      this.delRowIndexList.map(index => {
        if (Number(index) === rowIndex) {
          console.log(rowIndex);
          return "del-row";
        }
        return "";
      });
    },

    // checkbox
    handleSelectionChange(selection) {
      this.delRowIndexList = [];
      this.multipleSelection = selection;
      for (let i in selection) {
        for (let j in this.tableData) {
          if (selection[i].id == this.tableData[j].id) {
            this.delRowIndexList.push(j);
            continue;
          }
        }
      }
    }
  }
};
</script>

<style lang="less" scoped>
.table {
  margin-top: 10px;
}
.el-table .del-row {
  color: red;
  font-size: 26px;
}
// 修改 table 样式
/deep/.el-table {
  th {
    background-color: #d3dfe7;
    text-align: center;
    padding: 0;
    height: 40px;
    > .cell {
      color: #12222e;
      font-weight: 400;
    }
    .el-checkbox__input {
      &.is-checked {
        .el-checkbox__inner {
          border-color: #0092ff;
        }
      }
      .el-checkbox__inner {
        background-color: #d3dfe7;
        border: 1px solid #b5b5b5;
        &::after {
          border-color: #0092ff;
        }
      }
      &.is-indeterminate {
        .el-checkbox__inner {
          border-color: #0092ff;
          &::before {
            background-color: #0092ff;
          }
        }
      }
    }
  }
  tr {
    background-color: #eff9ff;
    &:hover {
      background-color: #e1e9ef;
      // background-color: red;
      > td {
        background-color: #e1e9ef;
      }
    }
    td {
      color: #12222e;
      text-align: center;
      // 详细信息
      &:last-child {
        > .cell {
          color: #0092ff;
          cursor: pointer;
          user-select: none;
        }
      }
      .el-checkbox__input {
        &.is-checked {
          .el-checkbox__inner {
            border-color: #0092ff;
          }
        }
        .el-checkbox__inner {
          background-color: #eff9ff;
          border: 1px solid #b5b5b5;
          &::after {
            border-color: #0092ff;
          }
        }
      }
    }
  }
}
</style>
